// Import React Component
import React from "react";

// Import Reactstrap Components
import { Container } from "reactstrap";

// Import Styles
import "./Stadium.style.scss";

// Import Translation Module
import { useTranslation } from "react-i18next";

// Import Stadium Header Background Image
import headerBackground from "./stadiumHeaderBackground.jpg";

// Import React Icons
import { AiOutlineDoubleRight } from "react-icons/ai";

function Stadium() {
  const { t } = useTranslation();

  return (
    <>
      <section id="stadium" className="stadium">
        <header>
          <img src={headerBackground} alt="" />
          <Container>
            <div className="stadium__heading">
              <div className="stadium__heading__tag">
                <h1>{t("Stadium, Metro, Train Station")}</h1>
              </div>
              <div className="stadium__heading__breadcrumbs">
                <ul>
                  <li>
                    <a href="#!">
                      {t("homepage")} <AiOutlineDoubleRight size="14" />
                    </a>
                  </li>
                  <li>
                    <a href="#!">
                      {t("products & usage areas")}{" "}
                      <AiOutlineDoubleRight size="14" />
                    </a>
                  </li>
                  <li>
                    <a href="#!">{t("stadium, metro & train station")}</a>
                  </li>
                </ul>
              </div>
            </div>
          </Container>
        </header>
        <main>
          <article className="stadium__description">
            <Container>
              <div className="stadium__description__content">
                <b>{t("Stadium, Subway, Train Station")}</b>
                <p>
                  {t(
                    "These units are produced to withstand heavy traffic during peak travel hours for the transfer of the masses. The units are designed with passenger density in mind The simple and vandal-resistant units are manufactured to provide safe and fast transportation."
                  )}
                </p>
              </div>
            </Container>
          </article>
          <article className="stadium__products">
            <Container>
              <div className="stadium__products__heading">
                <h2>{t("Our Products for Stadium, Metro, Train Station")}</h2>
              </div>
            </Container>
          </article>
        </main>
      </section>
    </>
  );
}

export default Stadium;
