import React from "react";
import HouseAccordion from "./Accordion";

import { useTranslation } from "react-i18next";

const HouseSection = () => {
  const { t } = useTranslation();
  return (
    <div className="container">
      <h1 className="title">{t("Housing")}</h1>
      <p className="text">
        {t(
          "An elevator experience, with a simple but stylish design, that can be safely used by all family members in your home. We provide safe and fast transportation with our Eco Line and Eco B Line residential elevators, stair-type disability platform lifts and seat-type stairlifts."
        )}
      </p>
      <br />
      <h1 className="accordion-title">{t("Our Products for House")}</h1>
      <HouseAccordion />
    </div>
  );
};

export default HouseSection;
