import React from "react";
import { useTranslation } from "react-i18next";

const CareersTextPage = () => {
  const { t } = useTranslation();
  return (
    <div className="CareersTextPage">
      <div className="wrapper">
        <h1 className="title">{t("Who we are")}</h1>
        <br />
        <br />
        <p>
          {t(
            "Beta Lift Asia is the world's largest manufacturer of elevators and escalators, installation and service. We provide services to customers in more than 200 countries and territories around the world and serve more than 2 million elevators and escalators. We have more than 69,000 employees, including engineers, digital specialists, sales and technical specialists, as well as factory and field technicians. We are proud to be a global team that continues to take on the challenges of a highly efficient, fast-growing company. Together, Otis employees create new ideas and opportunities by working together in different time zones, geographies and cultures to better serve our customers. Just imagine the heights your career can reach!"
          )}
        </p>
      </div>
    </div>
  );
};

export default CareersTextPage;
