// Import React
import React from "react";

// Reactstrap Components
import { Container, Row, Col } from "reactstrap";

// Import Local Component
import ProductItem from "../ProductItem";

// Import Backogrund Image
import BackgroundImage from "../usageAreasBackground.jpg";

// Import Product Items Images
import productItem1 from "../productItem3.jpg";
import productItem2 from "../productItem5.jpg";

// Styles
import "./UsageAreas.style.scss";
import UsageAreaNav from "./usageAreaNav";
import "./UsageAreas.style.scss";

// Import Translation Module
import { useTranslation } from "react-i18next";

// Usage Areas Component
function UsageAreas() {
  const { t } = useTranslation();

  return (
    <section id="usageAreas" className="usageAreas">
      <UsageAreaNav />
      <header>
        <img src={BackgroundImage} alt={t("Usage Areas Background")} />
        <h1 className="text-center">{t("Usage Areas")}</h1>
      </header>
      <main>
        <article className="main-part">
          <Container>
            <Row className="d-flex flex-column align-items-center">
              <Col className="main-info col-12 col-lg-7 text-center">
                <h2>{t("Our products have many usage areas")}</h2>
                <p>
                  {t(
                    "Lorem ipsum dolor sit amet consectetur adipisicing elit. Illo ipsa culpa assumenda, deserunt quo earum laudantium at cupiditate quibusdam laboriosam nam rem iusto praesentium temporibus recusandae corrupti quos rerum error dolore magnam exercitationem. Dolorum minima sint consequatur voluptate nisi. Nisi!"
                  )}
                </p>
                <p>
                  {t(
                    "Lorem ipsum dolor sit amet consectetur adipisicing elit. Nihil assumenda sequi accusamus fugiat dolore voluptatem."
                  )}
                </p>
                <a href="#!">{t("contact us")}</a>
              </Col>
              <Col className="main-products col-12 text-center">
                <h4>{t("elevator products")}</h4>
                <ProductItem image={productItem1} title="Skybuild™" />
                <ProductItem image={productItem2} title="EMS Panorama 2.0" />
              </Col>
            </Row>
          </Container>
        </article>
      </main>
    </section>
  );
}
export default UsageAreas;
