import React from "react";

import News_LastPage from "./News_LastPage";
import { useTranslation } from "react-i18next";
import AboutImage1 from "../img/mediacontact_OB.jpg";
import AboutImage2 from "../img/mediaassets_OB.jpg";

function News_LastPage_Img() {
  const { t } = useTranslation();

  const object = [
    {
      title: t("Media Contacts"),
      src: AboutImage1,
      path: "#",
    },
    {
      title: t("Media Assets"),
      src: AboutImage2,
      path: "#",
    },
  ];
  return (
    <div>
      <section className="News_LastPage_Imgg">
        {object.map((img) => (
          <News_LastPage
            src={img.src}
            path={img.path}
            title={img.title}
            key={img.title}
          />
        ))}
      </section>
    </div>
  );
}

export default News_LastPage_Img;
