import React from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import "./HealthStyle.scss";

const HealthShowcase = () => {
  const { t } = useTranslation();

  return (
    <div className="health-showcase">
      <div className="text-wrapper">
        <h1 className="title">{t("This is a health page")}</h1>
        <br />
        <p>
          {t(
            "Join the Beta Lift Asia family, where collaboration, innovation and empowerment help everyone and the company reach new heights"
          )}
        </p>
      </div>
    </div>
  );
};

export default HealthShowcase;
