import React from "react";
import Card from "./Card";
import img1 from "./images/card-1.jpg";
import img2 from "./images/card-2.jpg";
import img3 from "./images/card-3.jpg";
import img4 from "./images/card-4.jpg";
import img5 from "./images/card-5.jpg";
import { useTranslation } from "react-i18next";

const Cards = () => {
  const { t } = useTranslation();
  const cardImages = [
    {
      name: "img - 1",
      path: img1,
      title: t("Update your elevator"),
      subtitle: t("HydroAccelTM"),
    },
    {
      name: "img - 2",
      path: img2,
      title: t("Mid-rise and high-rise buildings"),
      subtitle: t("Gen2® modernization"),
    },
    {
      name: "img - 3",
      path: img3,
      title: t("Upgrade your Mid-rise Elevator"),
      subtitle: t("Elevonic® R"),
    },
    {
      name: "img - 4",
      path: img4,
      title: t("High-rise buildings"),
      subtitle: t("SkyRise® modernization"),
    },
    {
      name: "img - 5",
      path: img5,
      title: t("Next generation destination dispatch"),
      subtitle: t("Compass® 360"),
    },
  ];

  return (
    <div className="cards">
      <div className="cards-wrapper">
        {cardImages.map((img) => (
          <Card key={img.name} img={img} />
        ))}
      </div>
    </div>
  );
};

export default Cards;
