import React from "react";
import Carousel from "react-bootstrap/Carousel";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import firstImg from "./images/slide-img-1.jpg";
import secondImg from "./images/slide-img-2.jpg";
import thirdImg from "./images/slide-img-3.jpg";
import fourthImg from "./images/slide-img-4.jpg";
import fifthImg from "./images/slide-img-5.jpg";
import sixthImg from "./images/slide-img-6.jpg";

function CarouselComponent() {
  const { t } = useTranslation();
  return (
    <div className="Carousel">
      <Carousel fade>
        <Carousel.Item>
          <img className="d-block w-100" src={firstImg} alt="First slide" />
          <div className="carousel-item-text-wrapper">
            <h2 className="carousel-title ">{t("The new Gen3™ elevator")}</h2>
            <p>{t("Meet our fully connected platform.")}</p>
            <Link to="#" className="carousel-button">
              {t("Learn More")}
            </Link>
          </div>
        </Carousel.Item>

        <Carousel.Item>
          <img className="d-block w-100" src={secondImg} alt="Second slide" />
          <div className="carousel-item-text-wrapper">
            <h2>
              {t('"Connected Elevators')} <br className="break" />
              {t("is our collective future.")} <br className="break" />
              {t('Welcome to Tomorrow"')}
            </h2>
            <p>{t("-Judy Marks, Beta Lift Asia president & CEO")}</p>
            <br />
            <br />
            <p>
              {t(
                "Beta Lift Asia has given world a glimpse into future of vibrant cities."
              )}
            </p>
            <Link to="#" className="carousel-button  mr-2">
              {t("Register")}
            </Link>
            <Link to="#" className="carousel-button ">
              {t("Learn How")}
            </Link>
          </div>
        </Carousel.Item>

        <Carousel.Item>
          <img className="d-block w-100" src={thirdImg} alt="Third slide" />
          <div className="carousel-item-text-wrapper">
            <h2 className="carousel-title ">
              {t("Made to Move Communities")}
              <sup>TM</sup>
            </h2>
            <p>
              {t(
                "Students around the globe spent eight weeks working with Beta Lift Asia mentors and using STEM skills to solve mobility challenges in their communities."
              )}
            </p>
            <Link to="#" className="carousel-button">
              {t("EXPLORE MADE TO MOVE COMMUNITIES")}
            </Link>
          </div>
        </Carousel.Item>

        <Carousel.Item>
          <img className="d-block w-100" src={fourthImg} alt="Third slide" />
          <div className="carousel-item-text-wrapper">
            <h2 class="carousel-title ">
              {t("Simplify your service selection")}
            </h2>
            <p>
              {t(
                " When it comes to service needs, every building is different. That’s why Beta Lift Asia is here to help you find the right service plan with six simple questions."
              )}
            </p>
            <Link to="#" className="carousel-button">
              {t("GET STARTED")}
            </Link>
          </div>
        </Carousel.Item>

        <Carousel.Item>
          <img className="d-block w-100" src={fifthImg} alt="Third slide" />
          <div className="carousel-item-text-wrapper">
            <h2 class="carousel-title ">{t("Upgrades made easy")}</h2>
            <p>
              {t(
                "With our eCatalog, Beta Lift Asia can help you quickly find the products and services you need to upgrade your elevator and escalator systems and stay ahead of future needs."
              )}
            </p>
            <Link to="#" className="carousel-button">
              {t("EXPLORE UPGRADE OPTIONS")}
            </Link>
          </div>
        </Carousel.Item>

        <Carousel.Item>
          <img className="d-block w-100" src={sixthImg} alt="Third slide" />
          <div className="carousel-item-text-wrapper">
            <h2 class="carousel-title ">{t("Your vision in motion")}</h2>
            <p>
              {t(
                "Our Beta Lift Asia Create tool can help simplify elevator system planning, with recommended configurations, customized performance details and more – all in a few clicks."
              )}
            </p>
            <Link to="#" className="carousel-button">
              {t("CREATE AND CONFIGURE")}
            </Link>
          </div>
        </Carousel.Item>
      </Carousel>
    </div>
  );
}

export default CarouselComponent;
