import React from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";

const Galleries = () => {
  const { t } = useTranslation();

  return (
    <div className="galleries">
      <div className="gallery-1">
        <div className="gallery-item-text">
          <div className="text">
            <h3>{t("Rabota v beta")}</h3>
            <br />
            <p>
              {t(
                "Through modernization we can provide an engaging and contemporary passenger experience. From clear stylish lobby controls and eye-catching aesthetics to advanced destination management and in-car technology, we want to inspire people on the move"
              )}
            </p>

            <br />
            <Link to="/contact">
              <button className="button-dark">CONTACT US</button>
            </Link>
          </div>
        </div>
        <div className="gallery-item-img-1"></div>
      </div>

      <div className="gallery-2">
        <div className="gallery-item-img-2"></div>
        <div className="gallery-item-text">
          <div className="text">
            <h4>{t("Akademiya")}</h4>
            <br />
            <b>Service drives us, just as it always has</b>
            <br />
            <p>
              {t(
                "We’ve always placed people at the heart of what we do, so naturally this approach shapes our customer service too. We’re committed to delivering the service you need."
              )}
            </p>
            <br />
            <br />
            <Link to="/contact">
              <button className="button-dark">CONTACT US</button>
            </Link>
          </div>
        </div>
      </div>

      <div className="gallery-1">
        <div className="gallery-item-text">
          <div className="text">
            <h3>{t("Traning")}</h3>
            <br />
            <p>
              {t(
                "Through modernization we can provide an engaging and contemporary passenger experience. From clear stylish lobby controls and eye-catching aesthetics to advanced destination management and in-car technology, we want to inspire people on the move"
              )}
            </p>

            <br />
            <Link to="/contact">
              <button className="button-dark">CONTACT US</button>
            </Link>
          </div>
        </div>
        <div className="gallery-item-img-3"></div>
      </div>

      <div className="gallery-2">
        <div className="gallery-item-img-4"></div>
        <div className="gallery-item-text">
          <div className="text">
            <h4>{t("Dliya v pesnikov")}</h4>
            <br />
            <b>Service drives us, just as it always has</b>
            <br />
            <p>
              {t(
                "We’ve always placed people at the heart of what we do, so naturally this approach shapes our customer service too. We’re committed to delivering the service you need."
              )}
            </p>
            <br />
            <br />
            <Link to="/contact">
              <button className="button-dark">CONTACT US</button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Galleries;
