import React from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";

function Showcase() {
  const { t } = useTranslation();

  return (
    <div className="CareersShowcase">
      <div className="text-wrapper">
        <h1 className="title">{t("Climb the career ladder")}</h1>
        <br />
        <p>
          {t(
            "Join the Beta Lift Asia family, where collaboration, innovation and empowerment help everyone and the company reach new heights"
          )}
        </p>
        <Link to="#">
          <button className="showcase-btn">{t("WORK IN THE COMPANY")}</button>
        </Link>
        <Link to="#">
          <button className="showcase-btn mb-5">{t("CONTACT US")}</button>
        </Link>
      </div>
    </div>
  );
}

export default Showcase;
