import React from "react";
import { useTranslation } from "react-i18next";
import AccordionAtom from "../usageAreaComponentsAtom/accordion/AccordionAtom";
import Showcase from "../usageAreaComponentsAtom/showcase/showcase";
import TextPage from "../usageAreaComponentsAtom/Text/TextPage";

const Solutions = () => {
  const { t } = useTranslation();
  const showcaseTitle = t("This is a Solutions page");
  const showcaseText = t(
    "AKE combines technology and aesthetics to produce its panoramic elevators.  They bring elegance to buildings with their wide panoramic view. Our panoramic elevator cabins are divided into three models; standard, mushroom and rocket."
  );
  const textPageTitle = t("Special Solutions");
  const textPageText = t(
    "With MY Lift, we offer transport solutions for existing and new structures that have reduced pit and top floor dimensions. We provide the transport solution for all kinds of structural challenges including those for rough terrain with our Slope Elevators."
  );
  const textPageSubtitle = t("Our Products for Special Solutions");

  const accordionTexts = [
    {
      accordionHeaderTitle: t(" Special Solutions / MY LIFT "),
      accordionBodyTitle: t("MY LIFT"),
      accordionText: t(
        `AKE Special Solutions Series offers a future-oriented system solution for versatile usage
          Security
           Systems comply with TS-EN 81-70, TS-EN 81-71 EN 81-1 + A3: 2011, EN81-21: 2011 standards
        Productivity
         Modern, efficient next generation motor
         Frequency control
        Design
         Various combination options
        Comfort
         Available cabin space`
      ),
    },
    {
      accordionHeaderTitle: t("Elevators Systems / Inclined Elevator"),
      accordionBodyTitle: t("Inclined Elevator"),
      accordionText: t(
        "We combine design and flexibility with our Inclined Elevators. These units provide ease of transfer through technological solutions on landscapes with structural challenges. Our elevators with a closed cabin system,  provide transportation solutions especially in touristic places, for the transfer on sloping terrain."
      ),
    },
  ];

  return (
    <div>
      <Showcase
        title={showcaseTitle}
        text={showcaseText}
        className={"solutions"}
      />
      <TextPage
        textTitle={textPageTitle}
        text={textPageText}
        subtitle={textPageSubtitle}
      />
      <AccordionAtom accordionTexts={accordionTexts} />
    </div>
  );
};

export default Solutions;
