import React from "react";
import { Link } from "react-router-dom";
import Section_4 from "./Section-4";
import { useTranslation } from "react-i18next";
import AboutImage1 from "../../../assets/img/Otis_OurCompany_newsroom_link_image.jpg";
import AboutImage2 from "../../../assets/img/Otis_OurCompany_careers_bottom_page_link_image.jpg";

function Section_3() {
  const { t } = useTranslation();

  const object = [
    {
      title: t("Newsroom"),
      src: AboutImage1,
      path: "#",
    },
    {
      title: t("Careers"),
      src: AboutImage2,
      path: "#",
    },
  ];
  return (
    <div>
      <section className="aboutus-section-3">
        {object.map((img) => (
          <Section_4
            src={img.src}
            path={img.path}
            title={img.title}
            key={img.title}
          />
        ))}
      </section>
    </div>
  );
}

export default Section_3;