import React from "react";
import { useTranslation } from "react-i18next";
import Showcase from "../usageAreaComponentsAtom/showcase/showcase";
import TextPage from "../usageAreaComponentsAtom/Text/TextPage";

const Restaurants = () => {
  const { t } = useTranslation();
  const showcaseTitle = t("This is a Factory-restaurant page");
  const showcaseText = t(
    "Join the Beta Lift Asia family, where collaboration, innovation and empowerment help everyone and the company reach new heights"
  );
  const textPageTitle = t("Factory, Restaurant, Marine, Logistics Centers");
  const textPageText = t(
    "We offer ease of transport for your belongings of different weights and sizes. It is used for transporting materials in ships, cafes and restaurants, hospitals, libraries. AKE freight elevators of all types and sizes are designed and produced in accordance with the specifications of the factories, warehouses and any type of building."
  );
  const textPageSubtitle = t("Our Products for Factory, Restaurant, Ship");
  return (
    <div>
      <Showcase
        title={showcaseTitle}
        text={showcaseText}
        className={"restaurants"}
      />
      <TextPage
        textTitle={textPageTitle}
        text={textPageText}
        subtitle={textPageSubtitle}
      />
    </div>
  );
};

export default Restaurants;
