import React from "react";
import "./TextPage.scss";
const TextPage = ({ textTitle, text, subtitle }) => {
  return (
    <div className="text-page">
      <div className="container">
        <b>{textTitle}</b>

        <p className="text-page__text">{text}</p>

        <h1>{subtitle}</h1>
      </div>
    </div>
  );
};

export default TextPage;
