import React from "react";
import { useTranslation } from "react-i18next";

function Text() {
  const { t } = useTranslation();
  return (
    <div className="container">
      <div className="text-wrapper">
        <h1 className="title">
          {t("Taking ideas and innovation to all-new levels")}
        </h1>
        <p>
          {t(
            "At Beta Lift Asia, we are Made to Move You. And that means more than enabling passenger journeys. It’s about pushing the boundaries of what’s possible for an entire industry, taking ideas and innovation to all-new levels and reinventing entire experiences for passengers and customers."
          )}
        </p>

        <br />
        <br />
        <p>
          {t(
            "Every day, Otis is bringing passion and expertise to address new challenges and uncover new possibilities. We’re partnering with leading researchers to provide science-based answers and guidance when they’re needed most. We’re empowering customers to take advantage of artificial intelligence and the internet of things (IoT). And we’re inspiring a new generation of engineers and leaders that will drive the industry forward."
          )}
        </p>
        <br />
        <br />
        <p>
          {t(
            "After all, when you create and lead an industry that’s all about movement, standing still is never an option. Check out the latest in innovation and thought leadership at Otis, and see where we can go together."
          )}
        </p>
      </div>
    </div>
  );
}

export default Text;
