import React from "react";
import { FaAngleDoubleRight, FaFacebookF, FaTelegram } from "react-icons/fa";
import { BsFillTelephoneFill } from "react-icons/bs";
import { ImLocation } from "react-icons/im";
import { FaFax } from "react-icons/fa";
import { FaEnvelope } from "react-icons/fa";
import { AiOutlineInstagram } from "react-icons/ai";
import { VscTwitter } from "react-icons/vsc";
import { TiSocialYoutubeCircular } from "react-icons/ti";
import { FaWhatsapp } from "react-icons/fa";
import { SiPinterest } from "react-icons/si";
import { AiFillLinkedin } from "react-icons/ai";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import "./ContactPage.style.scss";
function ContactPage() {
  const { t } = useTranslation();
  return (
    <>
      <div className="test flex-column">
        <div className="work__contact">
          <div className="work-item">
            <h2 className="text-white text-uppercase letter-spacing-5  font-weight-300 my-3">
              {t("CONTACT")}
            </h2>
          </div>
        </div>
        <div className="work-item2 mt-2">
          <Link className="link" href="/">
            {t("Homepage")}
          </Link>
          <FaAngleDoubleRight />
          {t("CONTACT")}
        </div>
      </div>
      <div className="container py-5">
        <div className="row  m-0">
          <div className="col-lg-6">
            <div className="contact-title ">{t("CONTACT INFORMATIONS")}</div>

            <ul class="contact-list">
              <li>
                <ImLocation />
                {t(
                  "Organize Sanayi Bölgesi, 1. Kısım, Atatürk Bulvarı, 07193  Döşemealtı / Antalya"
                )}
              </li>

              <li>
                <BsFillTelephoneFill className="icon" />
                <Link to=" tel:+90 242 258 15 71">+90 242 258 15 71</Link>
              </li>

              <li>
                <BsFillTelephoneFill className="icon" />
                <Link to="tel:+90 242 258 15 71">+90 242 258 15 71</Link>
              </li>
              <li>
                <FaFax className="icon" />
                <Link to="tel: +90 242 258 15 69">+90 242 258 15 69</Link>
              </li>

              <li>
                <FaEnvelope className="icon" />
                <Link to="mailinfo:info@ake.com.tr">info@ake.com.tr</Link>
              </li>
              <br />
            </ul>

            <div className="list">
              <ul className="mb-5">
                <li>
                  <span>{t("İSTANBUL")}</span>{" "}
                  {t("İkitelli OSB Mutsan M12 Başakşehir / İstanbul")}
                </li>
                <br />

                <li>
                  <span>{t("ANKARA")}</span>{" "}
                  {t(
                    "Batı Sitesi Mah. Gersan Sanayi Sitesi 2307/3 sokak no : 24 Yenimahalle / Ankara"
                  )}
                </li>

                <li>
                  <span> {t("SAMSUN:")}</span>{" "}
                  {t("Yenimahalle 3243 sokak no: 36 Atakum Samsun")}
                </li>

                <li>
                  <span>{t("İZMİR:")}</span>{" "}
                  {t(
                    "Kemalpaşa Cad. Yalçın İş Merkez Karacaoğlan No: 295 Daire: 25  Bornova / İzmir"
                  )}
                </li>
                <li>
                  <span>
                    <strong>{t("Call Center 444 13 53")}</strong>Call Center 444
                    13 53
                  </span>
                </li>
              </ul>
              <hr />
            </div>

            <div className="link-icon d-flex flex-wrap">
              <div
                className="iconInformation d-flex justify-content-center align-items-center rounded-circle border me-3 border-dark"
                style={{ width: "45px", height: "45px" }}
              >
                <Link to="https://www.facebook.com/">
                  <FaFacebookF />
                </Link>
              </div>
              <div
                className="iconInformation d-flex justify-content-center align-items-center rounded-circle border me-3 border-dark"
                style={{ width: "45px", height: "45px" }}
              >
                <Link to="https://www.instagram.com/?hl=ru">
                  <AiOutlineInstagram />
                </Link>
              </div>
              <div
                className="iconInformation d-flex justify-content-center align-items-center rounded-circle border me-3 border-dark"
                style={{ width: "45px", height: "45px" }}
              >
                <Link to="https://api.whatsapp.com/">
                  {" "}
                  <VscTwitter />
                </Link>
              </div>
              <div
                className=" iconInformation d-flex justify-content-center align-items-center rounded-circle border me-3 border-dark"
                style={{ width: "45px" }}
              >
                <Link to="https://www.youtube.com/">
                  <TiSocialYoutubeCircular />
                </Link>
              </div>
              <div
                className="iconInformation d-flex justify-content-center align-items-center rounded-circle border me-3 border-dark"
                style={{ width: "45px", height: "45px" }}
              >
                <Link to="https://www.whatsapp.com/?lang=ru">
                  <FaWhatsapp />
                </Link>
              </div>
              <div
                className=" iconInformation d-flex justify-content-center align-items-center rounded-circle border me-3 border-dark"
                style={{ width: "45px", height: "45px" }}
              >
                <Link to="https://www.pinterest.com/">
                  <SiPinterest />
                </Link>
              </div>
              <div
                className="iconInformation d-flex justify-content-center align-items-center rounded-circle border me-3 border-dark"
                style={{ width: "45px", height: "45px" }}
              >
                <Link to="https://www.linkedin.com/home/?originalSubdomain=ru">
                  <AiFillLinkedin />
                </Link>
              </div>
            </div>
          </div>
          <div className="col-lg-6 mt-5 m-lg-0">
            <div className="contact-title">{t("CONTACT FORM")}</div>
            <form className="contact-form">
              <div className="contact-1 ">
                <label className="fw-bold">{t("Name, Surname")}</label>
                <div className="form-title">
                  <input
                    id="surname"
                    name="name-surname"
                    type="text"
                    maxLength="50"
                    placeholder="Name, Surname"
                    required
                    className="form-control"
                  />
                </div>
                <label className="fw-bold">{t("Phone Number")}</label>

                <div className="form-title">
                  <input
                    id="number"
                    name="phone-number"
                    type="text"
                    maxLength="20"
                    placeholder="Phone, Number"
                    required
                    className="form-control"
                  />
                </div>
                <label className="fw-bold">{t("Phone Number")}</label>

                <div className="form-title">
                  <input
                    id="addre"
                    name="e-mail-address"
                    type="text"
                    maxLength="50"
                    placeholder="E-mail address"
                    required
                    className="form-control"
                  />
                </div>
                <label className="fw-bold">{t("Your massage")}</label>
                <div className="form-group">
                  <textarea
                    name="your-massage"
                    type="textarea"
                    maxLength="500"
                    required
                    className="form-control"
                  ></textarea>
                </div>
                <div className="batr">
                  <button>{t("S U B M I T")}</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div className="section-map">
        <iframe
          className="w-100"
          height="500"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3182.565871342569!2d30.609603714788566!3d37.09164535894014!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x14c475e65da21097%3A0x997ddbd5e1838261!2sAke%20Asans%C3%B6r%20Fabrikasi!5e0!3m2!1str!2str!4v1621421275158!5m2!1str!2str"
        ></iframe>
      </div>
    </>
  );
}

export default ContactPage;
