import React from "react";
import { useTranslation } from "react-i18next";
import Showcase from "../usageAreaComponentsAtom/showcase/showcase";
import TextPage from "../usageAreaComponentsAtom/Text/TextPage";

const CarPark = () => {
  const { t } = useTranslation();
  const showcaseTitle = t("This is a car-park page");
  const showcaseText = t(
    "Join the Beta Lift Asia family, where collaboration, innovation and empowerment help everyone and the company reach new heights"
  );
  const textPageTitle = t("Car park");
  const textPageText = t(
    "Vehicle lifts, which are widely used in residential and business centers, bring alternative solutions to parking lots. With this unique application, AKE becomes a solution partner for the vehicle elevator needs of residences and workplaces."
  );
  const textPageSubtitle = t("Our Products for Car park");
  return (
    <div>
      <Showcase
        title={showcaseTitle}
        text={showcaseText}
        className={"car-park"}
      />
      <TextPage
        textTitle={textPageTitle}
        text={textPageText}
        subtitle={textPageSubtitle}
      />
    </div>
  );
};

export default CarPark;
