import React from "react";
import { useTranslation } from "react-i18next";
import Icon1 from "./images/whatmakesusunique.png";
import Icon2 from "./images/whatwelookfor.png";
import Icon3 from "./images/whatweoffer.png";
import ApartmentCreator from "./ApartmentCreator";

function Apartment() {
  const { t } = useTranslation();
  const obj = [
    {
      img: Icon1,
      title: t("WHAT MAKES US UNIQUE"),
      texts: [
        t("We respect the imagination"),
        t("We believe in ourselves"),
        t("We are a lot of voices"),
        t("We are better when we are together"),
        t("We strive to be better"),
      ],
    },
    {
      img: Icon2,
      title: t("WHAT WE ARE LOOKING FOR"),
      texts: [
        t("Imagination"),
        t("Cooperation"),
        t("Involvement"),
        t("Empathy"),
        t("Opportunity Development"),
        t("Pace"),
      ],
    },
    {
      img: Icon3,
      title: t("WHAT WE OFFER"),

      texts: [
        t("Exciting, focused work"),
        t(
          "A place where our people can be themselves and where the best idea wins"
        ),
        t("Significant career opportunities"),
        t("Reliable compensation and benefit packages"),
      ],
    },
  ];

  return (
    <div className="container">
      <div className="Apartment">
        <h1>{t("At Beta Lift Asia, it's our people that set us apart")}</h1>
        <div className="article">
          {obj.map((article) => (
            <ApartmentCreator
              img={article.img}
              title={article.title}
              text={article.texts}
              key={article.title}
            />
          ))}
        </div>
      </div>
    </div>
  );
}

export default Apartment;
