import React from "react";
import { useTranslation } from "react-i18next";
import Showcase from "../usageAreaComponentsAtom/showcase/showcase";
import TextPage from "../usageAreaComponentsAtom/Text/TextPage";

const Hotels = () => {
  const { t } = useTranslation();
  const showcaseTitle = t("This is a hotels page");
  const showcaseText = t(
    "Join the Beta Lift Asia family, where collaboration, innovation and empowerment help everyone and the company reach new heights"
  );
  const textPageTitle = t("Residence, Hotel, Convention Center");
  const textPageText = t(
    "Welcome your guests with the prestige of AKE. Our eye-catching designs, useful interior design and fast navigation makes a difference in your buildings. We offer you a safe and comfortable transportation experience with elevator, escalator and disability systems suitable for every building type."
  );
  const textPageSubtitle = t("Our Products for Hotels, Congress Center");
  return (
    <div>
      <Showcase
        title={showcaseTitle}
        text={showcaseText}
        className={"hotels"}
      />
      <TextPage
        textTitle={textPageTitle}
        text={textPageText}
        subtitle={textPageSubtitle}
      />
    </div>
  );
};

export default Hotels;
