import React from "react";
import { useTranslation } from "react-i18next";

import "./House.style.scss";

const HouseShowcase = () => {
  const { t } = useTranslation();

  return (
    <div className="house-showcase">
      <div className="text-wrapper">
        <h1 className="title">{t("This is a house page")}</h1>
        <br />
        <p>
          {t(
            "Join the Beta Lift Asia family, where collaboration, innovation and empowerment help everyone and the company reach new heights"
          )}
        </p>
      </div>
    </div>
  );
};

export default HouseShowcase;
