import React from "react";
import "./news.style.scss";
import { useTranslation } from "react-i18next";

function News_Intro() {
  const { t } = useTranslation();

  return (
    <div className="news-container">
      <section className="news-section-1">
        <div className="news-section-1-text">
          <h1>
            {t("Beta Lift Asia Declares Quarterly Dividend of $0.24 per Share")}
          </h1>
          <button className="new-section-1-btn">{t("READ THE ARTECLE")}</button>
        </div>
        <div></div>
      </section>
    </div>
  );
}

export default News_Intro;
