import React from "react";
import "react-bootstrap";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import Fade from "react-reveal/Fade";

function WhoWeAre() {
  const { t } = useTranslation();

  return (
    <div className="who-we-are">
      <div className="container px-5">
        <div className="text-wrapper">
          <Fade top>
            <p className="title">{t("WHO WE ARE")}</p>
          </Fade>
          <br />
          <Fade left>
            <h1>
              {t(
                "We are the world’s leading company for elevator and escalator manufacturing, installation and service."
              )}
            </h1>
          </Fade>
          <Fade right>
            <p>
              {t(
                "We move 2 billion people a day and maintain more than 2 million customer units worldwide - the world’s largest portfolio. We can be found in many of the world’s most recognizable buildings as well as the busiest transportation hubs and retail centers. We are everywhere people are on the move."
              )}
            </p>
          </Fade>
          <Fade left>
            <Link to="#" className="carousel-button">
              {t(" OUR COMPANY")}
            </Link>
          </Fade>
        </div>
        <div className="statistics">
          <Fade right>
            <h1>{t("Strength in numbers")}</h1>
          </Fade>

          <Fade left>
            <div className="numbers">
              <div className="net-sales">
                <h1>{t("$12.8B")}</h1>
                <p className="statistics-description">{t("NET SALES")}</p>
              </div>

              <div className="units-serviced">
                <h1>{"~2.1M"}</h1>
                <p className="statistics-description">{t("UNITS SERVICED")}</p>
              </div>

              <div className="people-moved-daily">
                <h1>{t("2B")}</h1>
                <p className="statistics-description">
                  {t("PEOPLE MOVED DAILY")}
                </p>
              </div>
            </div>{" "}
          </Fade>
        </div>

        <Fade right>
          <div className="buttons">
            <Link to="#" className="carousel-button mr-3">
              {t("ANNUAL REPORT")}
            </Link>
            <Link to="#" className="carousel-button">
              {t("FACTSHEET")}
            </Link>
          </div>
        </Fade>
      </div>
    </div>
  );
}

export default WhoWeAre;
