import React from "react";
import { useTranslation } from "react-i18next";
import AccordionAtom from "../usageAreaComponentsAtom/accordion/AccordionAtom";
import Showcase from "../usageAreaComponentsAtom/showcase/showcase";
import TextPage from "../usageAreaComponentsAtom/Text/TextPage";

const Panoramic = () => {
  const { t } = useTranslation();
  const showcaseTitle = t("This is a Panoramic page");
  const showcaseText = t(
    "AKE combines technology and aesthetics to produce its panoramic elevators.  They bring elegance to buildings with their wide panoramic view. Our panoramic elevator cabins are divided into three models; standard, mushroom and rocket."
  );
  const textPageTitle = t("Panoramic");
  const textPageText = t(
    "Vehicle lifts, which are widely used in residential and business centers, bring alternative solutions to parking lots. With this unique application, AKE becomes a solution partner for the vehicle elevator needs of residences and workplaces."
  );
  const textPageSubtitle = t("Our Products for Panoramic");

  const accordionTexts = [
    {
      accordionHeaderTitle: t(" Elevators Systems / Panoramic "),
      accordionBodyTitle: t("Panoramic"),
      accordionText: t(
        "AKE combines aesthetics and technology to produce the panoramic elevator to enhance the appearance of the building. The panoramic elevators, which are specially and individually designed based on the building, feature a glass cabin . Panoramic elevators are preferred in shopping malls, hotels, public buildings and entertainment centers to offer users a pleasant viewing experience of their …"
      ),
    },
  ];

  return (
    <div>
      <Showcase
        title={showcaseTitle}
        text={showcaseText}
        className={"panoramic"}
      />
      <TextPage
        textTitle={textPageTitle}
        text={textPageText}
        subtitle={textPageSubtitle}
      />
      <AccordionAtom accordionTexts={accordionTexts} />
    </div>
  );
};

export default Panoramic;
