import React from "react";
import { useTranslation } from "react-i18next";
import "./news.style.scss";
function NewsSection_3() {
  const { t } = useTranslation();

  return (
    <div>
      <section className="news-section-3 d-flex justify-content-center ">
        <div className="news-section_3_text">
          <p className="news-section-3-p">
            {t("Beta Lift Asia IN THE NEWS ")}
            <div className="news-section-3-line"></div>
          </p>

          <h1 className="news-section-3-h1">
            {t("That's what the world says about us")}
          </h1>
        </div>
      </section>
    </div>
  );
}

export default NewsSection_3;
